export const Addcricketcoupons = (add) => {
  return (dispatch) => {
    http
    .post('/cricket/contest_prizes/add_prize', add)
    .then((response) => {
      notifySuccessadd(response.message)
      dispatch({
        type: 'ADD_PRIZE_MASTER_CRICKET',
        data: response.data
      })    
    })
    .catch((err) => {
      console.log(err)
      notifySuccessdelete(err.message)
    })
  }
}
// api/cricket/contest_prizes/add_prize
