export const getprizecricketdetails = (id) => {
  return async (dispatch) => {
   await http
      .put(`/cricket/view/${id}`)
      .then((response) => {
        dispatch({
          type: 'PRIZE_DETAILS_CRICKET',
          data: response.data.data
        })
      })
      .catch((err) => console.log(err))
  }
}

